body {
    font-family: 'Source Serif 4', serif;
    color: black;
    background-color: #F9F3DF;
  }
  
  .AboutPage {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
    align-items: center;       
    justify-content: center; 
  }

  .AboutContent {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
    align-items: center;       
    justify-content: center; 
    text-align: left;
    margin-top: 50px;
}


.AboutTitle {
  font-size: 2.5em;
  margin-bottom: 20px;
}

.AboutBio {
  margin: 20px 0;
  font-size: 1.2em;
  line-height: 1.6;
}

.AboutBio p {
    margin: 20px 0;
    font-size: 0.8em;
    line-height: 1.6;
  }

  .AboutBio a {
    color: #333; /* Change this to your desired color */
    text-decoration: none; /* Removes underline */
    font-weight: bold;
}

.AboutPhotos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px; 
    padding: 30px;
    border-radius: 20px;
    background-color: #2f274e21;
  }
  
  .AboutPhotos  img{
    width: 270px;
    height: 270px;
    object-fit: cover;
  }


  /* Responsive Navbar Adjustments */
@media (max-width: 768px) {

  .AboutPhotos {
    display: grid;
    grid-template-columns: 1fr; /* Single column */
    grid-template-rows: repeat(3, auto); /* Three rows */
    gap: 10px; 
    padding: 30px;
    border-radius: 20px;
    background-color: #2f274e21;
    box-sizing: border-box; /* Ensures padding is included in width */
    width: 100%; /* Adjust width if needed */
    max-width: 100%; /* Prevents overflow on smaller screens */
  }
  
  .AboutPhotos img {
    width: 100%; /* Responsive to container width */
    height: auto; /* Maintains aspect ratio */
    max-height: 300px; /* Limit height if necessary */
    object-fit: cover; /* Scales image to fit without cropping */
  }
  
}


