
.SpeedworkPage {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
}

.SpeedworkContent {
    text-align: center;
    margin-top: 30px;
}


.speedworkLogo {
    height: 50px;
    width: 200px;
}

.InfoSection {
    margin-top: 20px;
    text-align: left;
    padding-bottom: 25px;
}

.InfoSection h1 {
    color: #333;
    text-align: left;
    margin-left: 50px;
    padding-bottom: 10px;
}

.InfoContent {
    background-color: #2f274e21;
    padding: 20px;
    border-radius: 12px;
    transition: transform 0.2s ease;
}


.InfoSection a {
    color: #333; /* Change this to your desired color */
    text-decoration: none; /* Removes underline */
    font-weight: bold;
}

.SpeedworkPhonePreviews {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px; 
  }
  
  .SpeedworkPhonePreviews img {
    width: 100%;
    object-fit: cover;
  }
  

  @media (max-width: 768px) {

    .SpeedworkPhonePreviews {
      display: grid;
      grid-template-columns: 1fr; /* Single column */
      grid-template-rows: repeat(3, auto); /* Three rows */
      gap: 10px; 
      width: 70%; /* Adjust width if needed */
      max-width: 70%; /* Prevents overflow on smaller screens */
      margin: 0 auto; /* Centers horizontally */
      text-align: center; /* Centers content inside */
    }
}