
.WindmatePage {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
}

.WindmateContent {
    text-align: center;
    margin-top: 30px;
}


.logo-title {
    display: flex;
    align-items: flex-end;
    margin-bottom: 25px;
  }

.windmateLogo {
    height: 250px;
    width: 250px;
    margin-right: -130px;
}
  

.InfoSection {
    margin-top: 20px;
    text-align: left;
    padding-bottom: 25px;
}

.InfoSection h1 {
    color: #333;
    text-align: left;
    margin-left: 50px;
    padding-bottom: 10px;
}

.InfoContent {
    background-color: #2f274e21;
    padding: 20px;
    border-radius: 12px;
    transition: transform 0.2s ease;
}


.InfoSection a {
    color: #333; /* Change this to your desired color */
    text-decoration: none; /* Removes underline */
    font-weight: bold;
}

.PhonePreviewsContainer {
    display: flex;
    justify-content: center; /* Centers the grid horizontally */
    width: 100%; /* Ensures the parent container takes full width */
  }
  
  .PhonePreviews {
    display: grid;
    width: 700px;
    height: auto;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
    text-align: center;
  }
  
  .PhonePreviews img {
    width: 100%;
    object-fit: cover;
  }

  @media (max-width: 768px) {

    .PhonePreviews {
      display: grid;
      grid-template-columns: 1fr; /* Single column */
      grid-template-rows: repeat(3, auto); /* Three rows */
      gap: 10px; 
      width: 70%; /* Adjust width if needed */
      max-width: 70%; /* Prevents overflow on smaller screens */
      margin: 0 auto; /* Centers horizontally */
      text-align: center; /* Centers content inside */
    }
}
  

  
  
  
  