body {
    font-family: 'Source Serif 4', serif;
    color: black;
    background-color: #F9F3DF;
  }
  
  .ContactPage {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
    align-items: center;       
    justify-content: center; 
  }

  .ContactContent {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
    align-items: center;       
    justify-content: center; 
    text-align: center;
    margin-top: 50px;
}

.ContactTitle {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.ContactTitle p {
    font-size: 0.75em;
  }


.ContactCards {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* Two equal-width columns */
    gap: 20px; /* Space between cards */
    border-radius: 16px;
    padding: 30px;
    padding-bottom: 20px;
    text-align: left;
  }
  
  .ContactCard {
    background-color: #2f274e21;
    padding: 20px;
    border-radius: 12px;
  }

  .ContactCard a {
    color: #333; /* Change this to your desired color */
    text-decoration: none; /* Removes underline */
    font-weight: bold;
    padding-left: 5px;
  }
  
  .ContactCard a:hover {
    color: white; /* Hover color for the link */
  }
  

  @media (max-width: 768px) {
    .ContactCards {
      display: grid;
      grid-template-columns: repeat(1, 1fr); /* Two equal-width columns */
      gap: 20px; /* Space between cards */
      border-radius: 16px;
      padding: 30px;
      padding-bottom: 20px;
      text-align: left;
    }
  }



  



